import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/Home.vue')
  },
  {
    path: '/products',
    name: 'products',
    component: () => import('../views/Products.vue')
  },
  {
    path: '/chart',
    name: 'Chart',
    component: () => import('../views/Chart')
  },
  {
    path: '/product',
    name: 'ProductView',
    component: () => import('../views/Product')
  },
  {
    path: '/product-realtime',
    name: 'ProductRealtimeView',
    component: () => import('../views/ProductRealtime')
  },
  {
    path: '/product-lifetime',
    name: 'ProductLifetimeView',
    component: () => import('../views/ProductLifetime')
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/About.vue')
  }
];

const router = new VueRouter({
  routes,
  scrollBehavior (/*to, from, savedPosition*/) {
    return { x: 0, y: 0 }
  }
});

export default router
